import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { supabase } from "../supabaseClient";
import toast from "react-hot-toast";

const UpdatePasswordPage = () => {
  const [newPassword, setNewPassword] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    const handleRecovery = async () => {
      const searchParams = new URLSearchParams(window.location.search);
      const access_token = searchParams.get("access_token");
      const refresh_token = searchParams.get("refresh_token");
      const type = searchParams.get("type");
  
      if (!access_token || !refresh_token || type !== "recovery") {
        toast.error("Token di recupero non valido.");
        return;
      }
  
      const { data, error } = await supabase.auth.exchangeCodeForSession({ access_token, refresh_token });
  
      if (error) {
        toast.error("Errore nella sessione.");
        console.error(error);
      } else {
        toast.success("Sessione recuperata!");
        console.log("User data:", data);
      }
    };
  
    handleRecovery();
  }, []);
  

  const handleUpdate = async () => {
    const { data: userData } = await supabase.auth.getUser();
    console.log(" User data:", userData);

    if (!userData?.user) {
      toast.error("Utente non autenticato");
      return;
    }

    const { error } = await supabase.auth.updateUser({ password: newPassword });

    if (error) {
      toast.error("Errore durante l'aggiornamento della password.");
      console.error(error);
    } else {
      toast.success("Password aggiornata con successo!");
      navigate("/");
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-900 text-white">
      <div className="bg-gray-800 p-6 rounded-lg w-full max-w-md">
        <h2 className="text-xl mb-4">Aggiorna la tua password</h2>
        <input
          type="password"
          className="w-full p-2 mb-4 rounded bg-gray-900"
          placeholder="Nuova password"
          value={newPassword}
          onChange={(e) => setNewPassword(e.target.value)}
        />
        <button
          onClick={handleUpdate}
          className="bg-blue-600 py-2 w-full rounded hover:bg-blue-700"
        >
          Aggiorna
        </button>
      </div>
    </div>
  );
};

export default UpdatePasswordPage;
